let paper;

module.exports = {
    importMagiana: () => {
        //Create loading avatar
        let magianaFaceSVG = require("../../../assets/face.svg");

        paper.project.importSVG(magianaFaceSVG, {
            onLoad: magianaGroup => {

                console.log(magianaGroup.children)

                magianaGroup.scale(2, magianaGroup.bounds.topLeft);


                magianaGroup.translate(new paper.Point(
                        paper.view.bounds.center.x - magianaGroup.bounds.width / 2,
                        paper.view.bounds.center.y - magianaGroup.bounds.height / 2
                ));
            }
        });
    },

    setPaper: p => {
        paper = p;
    }
};
